@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


.App{
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  font-family: 'Montserrat';
  display: flex;
  flex-direction: column;
  color: #fff;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.text{
  padding: 8px;
  border-radius: 6px;
  text-align: center;
  background-color: #111111;
  align-items: center;
}

.text-question{
  white-space: nowrap;
  padding: 8px;
  border-radius: 6px;
  text-align: center;
  font-size: 16px;
  background-color: #111111;
}

#logo{
  width: 128px;
  height: 128px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  margin-top: 10px;
  border-radius: 26px;
  border: 3px solid #fff;
}

.go-button{
  margin-top: 20px;
  display: flex;
  height: 40px;
}

