button{
  all:unset
}

.button{
    padding: 0 10px 0 10px;
    margin-bottom: 20px;
    border-radius: 2px;
    box-sizing: border-box;
    border: 0;
    text-align: center;
    border: 1px solid #4f4f4f;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat';
    background-color: transparent;
    color: white;
    font-weight: 600;
    font-size: 16px;
    display: flex;
    transition: border 0.3s ease-in-out, background-color 0.2s ease-in-out;
}
  
.button:hover{
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
}
  
.button:active{
  background-color: rgba(255, 255, 255, 0.2);
}

.button_completed{
  border: 1px solid green;
}

@media screen and (max-width:480px){
  .button{
    font-size: 14px;
  }
}

@media screen and (max-width:380px){
  .button{
    font-size: 13px;
  }
}